"use client";

import React from "react";

interface GDPData {
  country: string;
  year: number;
  gdp: number;
}

interface CountryGDP {
  country: string;
  gdp2000: number;
  gdp2023: number;
}

interface SlopeChartProps {
  data: GDPData[];
}

const formatGDP = (value: number) => {
  if (value >= 1e12) return (value / 1e12).toFixed(1) + "T";
  if (value >= 1e9) return (value / 1e9).toFixed(1) + "B";
  if (value >= 1e6) return (value / 1e6).toFixed(1) + "M";
  if (value >= 1e3) return (value / 1e3).toFixed(1) + "K";
  return value.toString();
};

const SlopeChart: React.FC<SlopeChartProps> = ({ data }) => {
  // Process data to get GDP for 2000 and 2023 per country
  const countryGDPs: CountryGDP[] = [];

  const countryMap = new Map<string, { gdp2000?: number; gdp2023?: number }>();

  data.forEach(({ country, year, gdp }) => {
    if (!countryMap.has(country)) {
      countryMap.set(country, {});
    }
    const entry = countryMap.get(country)!;
    if (year === 2000) {
      entry.gdp2000 = gdp;
    } else if (year === 2023) {
      entry.gdp2023 = gdp;
    }
  });

  countryMap.forEach((value, key) => {
    if (value.gdp2000 !== undefined && value.gdp2023 !== undefined) {
      countryGDPs.push({
        country: key,
        gdp2000: value.gdp2000,
        gdp2023: value.gdp2023,
      });
    }
  });

  // Sort countries by GDP 2023 descending
  countryGDPs.sort((a, b) => b.gdp2023 - a.gdp2023);

  // Calculate chart dimensions and scales
  const width = 800;
  const height = 500;
  const margin = { top: 40, right: 40, bottom: 60, left: 60 };
  const innerWidth = width - margin.left - margin.right;
  const innerHeight = height - margin.top - margin.bottom;

  // Get max GDP for scaling
  const maxGDP = Math.max(...countryGDPs.map(d => Math.max(d.gdp2000, d.gdp2023)));

  // Create scales
  const xScale = (value: number) => {
    if (value === 2000) return margin.left;
    if (value === 2023) return margin.left + innerWidth;
    return 0;
  };

  const yScale = (gdp: number) => {
    return margin.top + innerHeight - (gdp / maxGDP) * innerHeight;
  };

  return (
    <div className="max-w-5xl mx-auto p-4 font-sans text-gray-900 bg-white">
      <h2 className="text-2xl font-bold mb-6 text-center">GDP Comparison 2000 vs 2023</h2>
      <svg width={width} height={height}>
        {/* X axis */}
        <line
          x1={margin.left}
          y1={margin.top + innerHeight}
          x2={margin.left + innerWidth}
          y2={margin.top + innerHeight}
          stroke="#000"
          strokeWidth={1}
        />
        <text x={margin.left} y={margin.top + innerHeight + 20} textAnchor="middle">2000</text>
        <text x={margin.left + innerWidth} y={margin.top + innerHeight + 20} textAnchor="middle">2023</text>

        {/* Y axis */}
        <line
          x1={margin.left}
          y1={margin.top}
          x2={margin.left}
          y2={margin.top + innerHeight}
          stroke="#000"
          strokeWidth={1}
        />
        <text x={margin.left - 10} y={margin.top + 10} textAnchor="end">${formatGDP(maxGDP)}</text>
        <text x={margin.left - 10} y={margin.top + innerHeight - 10} textAnchor="end">$0</text>

        {/* Chart lines */}
        {countryGDPs.map(({ country, gdp2000, gdp2023 }) => (
          <g key={country}>
            <line
              x1={xScale(2000)}
              y1={yScale(gdp2000)}
              x2={xScale(2023)}
              y2={yScale(gdp2023)}
              stroke="#3b82f6"
              strokeWidth={2}
            />
            <circle
              cx={xScale(2000)}
              cy={yScale(gdp2000)}
              r={5}
              fill="#3b82f6"
            />
            <circle
              cx={xScale(2023)}
              cy={yScale(gdp2023)}
              r={5}
              fill="#3b82f6"
            />
            <text
              x={xScale(2000) - 10}
              y={yScale(gdp2000)}
              textAnchor="end"
              alignmentBaseline="middle"
              fontSize={12}
            >
              {formatGDP(gdp2000)}
            </text>
            <text
              x={xScale(2023) + 10}
              y={yScale(gdp2023)}
              textAnchor="start"
              alignmentBaseline="middle"
              fontSize={12}
            >
              {formatGDP(gdp2023)}
            </text>
            <text
              x={xScale(2000) - 10}
              y={yScale(gdp2000) - 15}
              textAnchor="end"
              fontSize={12}
              fontWeight="bold"
            >
              {country}
            </text>
          </g>
        ))}
      </svg>
    </div>
  );
};

export default SlopeChart;
